/*--ORIGINAL LESS---------------------------------------------------------------------------------------------------------------------------------------------*/

@import (less) 'color.less';
.btn {
    border-radius: 2px !important;
}

.button {
    font-weight: bold;
    font-size: 0.750em;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    text-transform: uppercase;
    height: 4.167em;
    min-width: 9.167em;
    box-shadow: none !important;
    // &:focus {
    //     box-shadow: 0 0 0.05em 0.2em rgba(37,137,0,0.6) !important;
    // }
    &.check.tabFocusOn {
        box-shadow: 0 0 0.05em 0.2em rgba(37, 137, 0, 0.6) !important;
    }
}

.tabFocusOn {
    box-shadow: 0 0 0.05em 0.2em rgba(37, 137, 0, 0.6) !important;
}

.button.default {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        color: @white;
        background-color: @green;
    }
    border-radius: 3px !important;
}

.button.default-l {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        color: @white;
        background-color: @green;
    }
    float: left;
    margin-left: 5px;
}

.button.default-r {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        color: @white;
        background-color: @green;
    }
    float: right;
}

.button-icon {
    padding-left: 0.625em;
    font-size: 2em !important;
    display: table-cell !important;
    //   top: -0.2125em;
    position: relative;
}

.button-label-icon {
    padding-left: 2.0em;
    padding-right: 2.625em;
    display: table-cell !important;
    vertical-align: middle;
    font: Arial 12 Bold;
}

// ---------------------------------------------------
// ---------------------------------------------------

.icomoon-Simboli_Risorsa-25 {
    display: none !important;
};

.button.round {
    border: 0;
    color: @green;
    background-color: transparent;
    border-color: @green;
    border-radius: 3px !important;
    &:hover{
        .icomoon-Simboli_Risorsa-25 {
            display: table-cell !important;
            color: @green;
        }
        .icomoon-Simboli_Risorsa-24 {
            display: none !important;
        }
    }
}

.button.round-l {
    border: 0;
    color: @green;
    background-color: transparent;
    border-color: @green;
    float: left;
    margin-left: 5px;
    &:hover{
        .icomoon-Simboli_Risorsa-25 {
            display: table-cell !important;
            color: @green;
        }
        .icomoon-Simboli_Risorsa-24 {
            display: none !important;
        }
    }
}

.button.round-r {
    border: 0;
    color: @green;
    background-color: transparent;
    border-color: @green;
    float: right;
    &:hover{
        .icomoon-Simboli_Risorsa-25 {
            display: table-cell !important;
            color: @green;
        }
        .icomoon-Simboli_Risorsa-24 {
            display: none !important;
        }
    }
}

.button-icon-round {
    font-size: 30px !important;
    display: table-cell;
    position: relative;
    border-color: @green;
    color: @darkest_gray;
    &:hover {
        color: @green;
    }
    // &:hover:not(.select) {
    //     color: @white;
    //     background-color: @green;
    // }
}

.button-label-icon-round {
    padding-left: 10px;
    padding-right: 10px;
    display: table-cell !important;
    vertical-align: middle;
    font: Arial 12 Bold;
    // &:hover:not(.select) {
    //     color: @white;
    //     background-color: @green;
    // }
}


:host-context(.contrasto-on) {
    .button.default-r, .button.default-l, .button.default {
        color: @black;
        background-color: @white;
        border-color: @black;
        &:hover:not(.select) {
            color: @white;
            background-color: @black;
            border-color: @white;
        }
    }
    .button.round-r, .button.round-l, .button.round {
        color: @black;
        background-color: @white;
        border-color: @black;
        &:hover{
            color: @black;
            .icomoon-Simboli_Risorsa-25 {
                color: @black;
            }
        }
    }
}